*, :before, :after {
  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
}

body {
  background-color: #e5e5cb;
}

.container {
  width: 95vw;
  text-align: center;
  margin: 0 auto;
  padding: 10px;
}

.search-form {
  width: 60%;
  background-color: #dae2b6;
  border-radius: 35px;
  justify-content: center;
  align-items: center;
  margin: 0 auto 15px;
  padding: 10px 0;
  display: flex;
}

.search-form > input {
  width: 225px;
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 20px;
  margin-right: 10px;
  padding: 15px;
  font-size: 17px;
}

.search-form > button {
  width: 55px;
  height: 55px;
  cursor: pointer;
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 50%;
  transition: all .3s ease-out;
}

.search-form > button:hover {
  color: #ffebb7;
  background-color: #243763;
}

.search-form > button:active {
  color: #ffebb7;
  background-color: #432c7a;
  border: 3px solid #dae2b6;
}

.gallery {
  flex-wrap: wrap;
  display: flex;
}

.photo-card {
  background-color: #fff8ea;
  border: 1px solid #000;
  border-radius: 9px;
  flex-wrap: wrap;
  flex-basis: 31%;
  margin: 5px;
  padding: 5px;
  display: flex;
}

img {
  width: 100%;
  border-radius: 9px;
  display: block;
}

.load-more {
  width: 180px;
  height: 52px;
  text-transform: uppercase;
  color: #ffebb7;
  cursor: pointer;
  background-color: #243763;
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 20px;
  margin-top: 10px;
  font-size: 14px;
  transition: all .3s ease-out;
}

.load-more:hover {
  color: #243763;
  background-color: #576f72;
  border: 1px solid rgba(0, 0, 0, 0);
}

.load-more:active {
  color: #243763;
  background-color: #dae2b6;
}

.load-more.is-hidden, .load-text.is-hidden, #load-animation.is-hidden {
  display: none;
}

#load-animation {
  width: 32px;
  height: 32px;
  margin: 0 auto;
  animation: loader1 3s linear infinite;
  display: block;
}

@keyframes loader1 {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

#load-animation span {
  width: 32px;
  height: 32px;
  clip: rect(16px, 32px, 32px, 0);
  animation: loader2 1.5s cubic-bezier(.77, 0, .175, 1) infinite;
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

@keyframes loader2 {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

#load-animation span:before {
  content: "";
  width: 32px;
  height: 32px;
  border: 3px solid rgba(0, 0, 0, 0);
  border-top-color: #fff;
  border-radius: 50%;
  animation: loader3 1.5s cubic-bezier(.77, 0, .175, 1) infinite;
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

@keyframes loader3 {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

#load-animation span:after {
  content: "";
  width: 32px;
  height: 32px;
  border: 3px solid #ffebb7;
  border-radius: 50%;
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.info {
  flex-direction: column;
  flex-basis: 90%;
  align-items: flex-start;
  margin: 10px auto 0;
  display: flex;
}

